/* VARIABLES */
#root {
  --color-red: #EE0909;
  --color-white: #ffffff;
  --color-yellow: #E87E00;
  --color-bright-yellow: #d4a821;
  --color-brik-red: #D83F31;
  --color-dark-purple: #28181B;
  --color-light-purple: #BA9A93;
  --color-green: #228b6e;
  --color-purple: #a52a96;
  
  --bgcolor: hsla(44, 100%, 7%, 1);
  --primary-color: hsla(44, 100%, 50%, 1);
  --secondary-color: hsla(284, 100%, 50%, 1);
  --tertiary-color: hsla(164, 100%, 50%, 1);


  --color-light-yellow: #D19E60;
  --color-black: #111211;
  --color-shadow: rgba(0, 255, 157, 0.7);
  --color-gray: #D9D9D9;
  --nav-font: 'Alegreya';
  --nav-button: 'Galindo';
  --title-font: 'Kanit';
  --button-font: 'Roboto Condensed';
  --normal-text-font: 'Cabin';
  --par-text: "Barlow Condensed";


  background-color: var(--bgcolor);
  color: black;
}

/* ----------------------------------------------------------------------------------------------------- */

/* APP */

.App{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  width: 100%;
}

#page-body{
  border-radius: 0 0 2rem 2rem;
  margin-top: auto;
  background: #ffffff15;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border-radius: 50px;
}
/* ----------------------------------------------------------------------------------------------------- */

/* NAVIGATION BAR */
.logo-photo{
  height: 3.5rem;
  /* width: rem; */
  /* border-radius: .5rem .5rem .5rem 5rem; */
  /* border-radius: 25rem; */
  /* padding-left: .5rem; */
  /* padding-right: 0; */
}
.nav_bar {
  overflow: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  gap: 1rem;
  background-color: var(--bgcolor);
  border-radius: 0 0 2rem 2rem;
}

.nav_link {
  color: white;
  font-family: var(--nav-font);
  font-size: 1.2rem;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  align-self:center;
  text-decoration: none;
  border: .1rem solid hsla(284, 100%, 5%, 1);
  /* background-color: #735300; */
  background-color: hsla(284, 100%, 15%, 1);
  width: 33%;
  padding:.5rem .5rem;
  transition:all .5s ease;
  letter-spacing:1px;
  outline:none;

}

#left_outer_link{
  width: 4rem;
  border-radius: 25rem;
  margin-right: 1rem;
  background-color: var(--bgcolor);
  border: 1px solid rgb(182, 135, 8);
}

#right_outer_link{
  border-radius: .5rem .5rem 2rem .5rem;
}

.inner_nav{
  border-radius: .5rem;
}

.nav_link:hover {
  background-color: var(--color-yellow);
  color: black;
  font-size: 1.5rem;
}

.nav_link:active{
  background-color: var(--primary-color);
  color: black;
}

@media(min-width: 800px){
  .nav_link{
    width: 20%;
  }
}
/* ----------------------------------------------------------------------------------------------------- */
/* ALL PAGE */

.margined-page{
  margin: 2rem 2rem;
  height: 100%;
}

.wrapped-page{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
} 


.left-border-decor{
  background-color: var(--color-dark-purple);
  margin-right: 2rem;
}

.normal_text{
  font-size: 18px;
  text-align: justify;
}

/* ----------------------------------------------------------------------------------------------------- */

/* HOME PAGE */

/* greetings */
.aboutMe-greeting{
  flex: 40%;
  display: flex;
  flex-direction: column;
  font-family: var(--button-font);
  font-weight: 400;
  font-size: 1.5rem;
  color: hsla(44, 100%, 75%, .5)
}

.aboutMe-greeting-container{
  grid-area: greeting;
  display: flex;
  align-items: center;
  width: 100%;
  text-align: center;

}
.profile-image-container{
  grid-area: image;
  display: flex;
  justify-self: center;
  align-items: center;
  padding: 2rem 0;
}
/* image */
.home-photo-container{
  width: 95%;
  border: 1px solid white;
}


/* description */

.firstName{
  color: hsla(44, 100%, 85%, 1);

}
.aboutMe-author{
  grid-area: title;
  font-family: var(--nav-font);
  font-weight: 400;
  font-size: 2.5rem;
  letter-spacing: 6px;
  text-align: center;
  color: hsla(44, 100%, 5%, 1)
}

.submit-button{
  min-width: 3rem;
  padding: .2rem 25px;
  font-family: var(--button-font);
  font-size: .8rem;
  background-color: #FFDE80;
  border: 2px solid #422800;
  border-radius: 30px;
  box-shadow: var(--bgcolor) 4px 4px 0 0;
  color: var(--bgcolor);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  line-height: 30px;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.submit-button:hover {
  background-color: hsla(44, 100%, 55%, 1);
}

.submit-button:active { 
  box-shadow: var(--bgcolor) 2px 2px 0 0;
  transform: translate(2px, 2px);
 }

#aboutMe-desc{
  grid-area: about;
  text-align: justify;
  display: flex;
  flex-direction: column;
  gap: .8rem;
}
#aboutMe-desc > span{
  font-weight: 600;
}

.aboutMe-contact-container{
  padding-top: 2rem;
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.aboutMe-tag-container{
  grid-area: icons;
  display: flex;
  justify-content: space-between;
}

#aboutMe-resume{
  text-decoration: none;
  color: var(--bgcolor);
  font-size: 1rem !important;
  text-align: center;
}

.aboutMe-contact-tags{
  font-size: .5rem !important;
  color: var(--bgcolor);
  padding-top: .3rem;
}

.aboutMe-profile_desc{
  grid-area: desc;
  display: grid;
  grid-template-areas: 
  "title" 
  "icons"
  "about"
  ;
  font-family: var(--normal-text-font);
  grid-row-gap: 2rem;
  width: 90%;
  justify-self: center;
  align-items: center;
}


/* closing image */
.closing-photo{
  height: 10rem;
  margin:0;
  /* rotate: -25deg; */
}
.closing-image-container{
  grid-area: flower;
  display: flex;
  justify-content: center;
  align-items: center;
}


/* home page display */
.home-page{
  display: grid;
  grid-template-areas: 
  "image"
  "greeting" 
  "desc"
  "flower"
  ;
}
.aboutMe-par{
  display: flex;
  font-family: var(--par-text);
  font-weight: 400;
  gap: 1rem;
  padding: .5rem 1rem;
  border-radius: .5rem;
  border-width: 1px 3px 6px 3px;
  border-color: var(--bgcolor);
  border-style: solid;
  color: hsla(44, 100%, 85%, 1);
}

.aboutMe-num{
  font-size: 3rem;
  flex-basis: 60px;
	flex-grow: 0;
	flex-shrink: 0;
  justify-self: center;
  align-self: center;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border-radius: 20%; */
  height: 100%;
  color: hsla(44, 100%, 45%, 1);
  border-right: 1px solid var(--bgcolor);
}
#aboutMe-num-0{
  /* background-color: hsla(164, 100%, 55%, 1); */
  background-color: hsla(44, 65%, 25%, .5);
}
#aboutMe-num-1{
  /* background-color: hsla(44, 100%, 55%, 1); */
  background-color: hsla(44, 85%, 45%, .5);
}
#aboutMe-num-2{
  /* background-color: hsla(284, 100%, 55%, 1); */
  background-color: hsla(44, 100%, 15%, .5);

}
#aboutMe-num-3{
  border: 0;
  color: hsla(44, 100%, 75%, 1);
  padding: .5rem .5rem;
}
.aboutMe-text, .aboutMe-goal{
  font-size: 18px;
  text-align: justify;
}
/* .aboutMe-text{
  border-bottom: 1px solid white;
} */
.aboutMe-goal{
  padding-top: .5rem;
}
@media(min-width: 800px){
  .aboutMe-author{
    font-weight: 600;
    font-size: 3.5rem;
    text-align: left;
    padding-top: 0;
    padding-bottom: 2rem;
    padding-left: 0;
    padding-right: 0;
  }
  .aboutMe-profile_desc{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    align-items: flex-start;
    width: 100%;
  }
  .aboutMe-contact-container{
    gap: 6rem;
    justify-content: center;
  }
  #aboutMe-desc{
    width: 100%;
    font-size: 1rem;
  }
  .aboutMe-contact-tags{
    font-size: .8rem !important;
  }
  #aboutMe-resume{
    font-size: 1.2rem !important;
  }
  .home-photo-container{
    height: 25rem;
    padding: 0;
  }
  .aboutMe-greeting{
    font-size: 2rem!important;
  }
  .home-page{
    grid-template-areas: 
    "image greeting" 
    "image desc"
    "flower flower"
    ;
    grid-template-columns: 45% 50%;
    grid-column-gap: 3rem;
    margin-top: 3rem;
    margin-left: 3rem;
    margin-right: 5rem;
  }
}
/* ----------------------------------------------------------------------------------------------------- */

/* SKILLS PAGE */
.work-left-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.work-photo-container{
  display: flex;
  justify-content: center;
  align-items: center;
}
.work-photo{
  max-height: 40%;
}
#work-horLine{
  background-color: var(--bgcolor);
}
.work-citation{
  flex:0.7;
  display: flex;
  flex-direction: column;
  font-family: var(--par-text);
  color: hsla(44, 100%, 85%, 1);
  font-size: 1.2rem;
  gap: 1.5rem;
}
.work-testimony-message{
  text-align: justify;
}
.work-quote-decor{
  display: flex;
  gap: 0.8rem;

}
.work-testimony, .work-testimony-author{
  width: 90%;
  padding: 1rem;
}
.quote-icons{
  font-size: 1.5rem;
  color: hsla(44, 100%, 50%, 1)
}
#quote-left{
  padding-right: 1rem;
}
#quote-right{
  padding-left: 1rem;
}


.work-testimony-author{
  text-align: right;
}
.work-right-container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center !important;
  gap: 2rem;
  --barItem-font-size: .6rem;
}

.work-skills_icons{
  width: 95%;
  display: flex;
  justify-content: space-between;
  padding-top: 3rem;
}

.work-button, .work-button-active{
  border-radius: 5rem;
  padding: 0.8rem 0;
  font-family: var(--button-font);
  font-size: .8rem;
  text-align: center !important;
  /* background-color: #157e60; */
  width: 6rem;

}
 .work-button{
  background-color: hsla(44, 100%, 75%, 1);
  border: 2px outset hsla(44, 100%, 95%, 1);
  box-shadow: 5px 10px 20px hsla(44, 100%, 95%, 1) inset;
  color: hsla(44, 100%, 55%, 1);
  /* color: #b1fce7; */

}

.work-button:hover, .work-button-active{
  border: 1px inset hsla(284, 100%, 55%, 1);
  box-shadow: 5px 10px 20px rgb(4, 20, 11) inset;
  /* background-color: #09382a; */
  background-color: hsla(284, 100%, 15%, 1);
  color: var(--bgcolor);
  -webkit-text-stroke: 1px black;

}
.work-button:active { 
  box-shadow: 5px 20px 40px rgb(4, 20, 11) inset;
 }


 @media(min-width: 800px){
  .work-right-container{
    --barItem-font-size: .9rem;
  }
  .work-skills_icons{
    justify-content: center;
    gap: 3rem;
  }
  .work-button, .work-button-active{
    width: 9rem;
    font-size: 1.5rem;
  }
 }
/* ----------------------------------------------------------------------------------------------------- */

/* EXPERIENCE PAGE */


.experienc-main-container{
  --exp-card-color : #C4B6A5;
  --card-height: 340px;
  --card-width: 410px;
  gap: .8rem;
  height: 100%;
  margin: 2rem .8rem 2rem 0rem;
}

.experience-hat-pic{
  width: var(--card-width);
  /* aspect-ratio: 1/1; */
}
.hexagon-container {

  width: var(--card-width);
  height: var(--card-height);
  position: relative;
  overflow: hidden;
  margin-top: 1em;
  margin-bottom: 1em;
}
.hexagon-container:hover .hexagon-frame {
  background-image: url('images/hexagon_frame.png');
}
.hexagon-container:hover .experience-ShowMe{
  display: none;
}
.hexagon-container:hover .experience-image{
  transition: all .3s;
  opacity: 0;
  -moz-transition: opacity 0.8s ease-in-out;
  -webkit-transition: opacity 0.8s ease-in-out;
  -ms-transition: opacity 0.8s ease-in-out;
  transition: opacity 0.8s ease-in-out;
}
.hexagon-container:hover .experience-image-text{
  transition: all .3s;
  opacity: 1;
}

.hexagon-frame {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('images/hexagon_frame_up.png');
  background-size: cover;
  margin-left: .5em;

}

.experience-column{
  margin-left: .8em;
  position: relative;
  z-index: 1;
  width: 80%;
  height: 70%;
  top: 40px; 
  left: 30px;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 0;
  display: flex;
  transition: all .3s;
  }
  #column0{ background-color: rgb(88, 24, 151); }
  #column1{ background-color: #E87E00; }
  #column2{ background-color: rgb(0, 128, 43); }
  #column3{ background-color: rgb(128, 0, 100); }
  #column4{ background-color: rgb(0, 13, 128); }
  #column5{ background-color: rgb(0, 128, 122); }

.experience-image{
  height: 60%;
  width: 65%;
  opacity: 0.9;
}
.experience-ShowMe{
  position: absolute;
  align-self: center;
  z-index:1;
  width: 70%;
  border-radius: .5rem;
  color: black;
  font-family: var(--nav-font);
  font-size: 1.2rem;
  font-weight: 600;
  background-color: rgba(255, 255, 255, 0.568);
  padding: 2rem;
  text-align: center;
}

.experience-image-text{
  margin-top: 2em;
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  position: absolute;
  align-self: center;
  z-index:1;
  width: 100%;
  height: 100%;
  display: flex;
  gap: .2rem;
  justify-content: center;
  align-items: center;
  font-family: var(--button-font);
  font-size: 1rem;
  opacity: 0;
  color: white;
}

.experience-skill-container{
  padding-top: .8rem;
  padding-bottom: .8rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: .7rem;
  font-size: .8rem;
  height: 4rem;
}

.experience-position{
  background-color: hsla(284, 100%, 55%, 1);
  border: 1px solid black;
  border-radius: .8rem;
  padding: .8rem .5rem;
  margin-bottom: 1rem;
  color: black;
}
#experience-horLine{
  background-color: black;
}
.experience-company, .experience-position{
  font-size: 1.1rem;
  font-weight: 400;
}
.experience-date{
  font-size: 1.3rem;
  padding-bottom: .5rem;
}
.experience-desc{
  padding-bottom: .9rem;
  text-align: center;
}
.experience-button{
  background-color: hsla(44, 100%, 75%, 1);
  color: hsla(164, 100%, 5%, 1);
  font-family: var(--nav-button);
  font-weight: 400;
  text-decoration: none;
  border: .2rem solid rgb(0, 24, 10);
  border-radius: .8rem;
  padding: .5rem 4rem;
  margin-top: 1em;
}
.experience-button:hover{
  background-color: var(--color-yellow);
  color: white;
}
.experience-button:active{
  translate: 0 .5rem;
}

.experience-code-tag{
  color: white;
  font-family: var('--font-button');
  font-size: 1em;
  font-weight: bold;
  background-color: hsla(164, 100%, 55%, .7);
  /* background-color: var(--color-brik-red); */
  padding: .5rem .8rem;
  border-radius: .5rem;
}
.experience-app-type{
  color: var(--color-red)
}

.experience-column:hover{
  /* background-color: #FFDE59; */
  /* border: 2px solid var(--color-yellow); */
  border-radius: 1rem;
}
@media(min-width: 800px){
  .hexagon-container {
    margin-left: 1em;
  }
  .hexagon-frame {
    margin-left: 1em;
    width: 95%;
  }

}


/* ----------------------------------------------------------------------------------------------------- */

/* GET IN TOUCH PAGE */

#getInTouch-center-container{
  padding: 25vh 1rem;
  display: flex;
  justify-content: center;
  align-items: center;

}

.getInTouch-column{

  display: flex;
  flex-direction: column;
  font-family: var(--normal-text-font);
  padding-bottom: 2rem;
  align-items: center;
}

.getInTouch-photo-container{
  display: flex;
  width: 100%;
  height: 50%;
  padding: 3rem 0 5rem 0; 
  justify-content: center;
}

.getInTouch-contact-info{
  font-family: var(--normal-text-font);
  padding-bottom: 2rem;
  color: black;
}
.getInTouch-tag{
  font-size: 1.2rem;
  padding-bottom: 1.5rem;
}

.getIntouch-tag-type{
  font-family: var(--button-font);
  font-weight: bold;
  padding-left: .5rem;
  font-size: 1.5rem;
  color: #FDA1AD;
}

.getInTouch-call-icons{
 display: flex;
 gap: .6rem;
 align-items: center;
}
.decor-icons{
  background-color: var(--color-dark-purple);
  border: solid #EC6D5F 3px;
  padding: .4rem .6rem;
  border-radius: 15rem;
  color: #EC6D5F;
}

.getInTouch-info{
  font-size: 1.2rem;
}

#column-right{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flower-image{
  width: 10rem;

}
.getInTouch-title-container{
  display: flex;
  align-items: center;
  padding-bottom: 2rem;
}

.getInTouch-title{
  flex:50%;
  font-weight: 700;
  font-size: 2.6rem;
  font-family: var(--button-font);
}

.bye-image{
  background-color: #E87E00;
  width: 330px;
}
.business-card-image{
  width: 95vw;
  border: 1px solid white;
}
@media(min-width: 800px){
  .getInTouch-column{
    align-items: flex-start;
  }
  .getInTouch-photo-container{
    justify-content: flex-start;
  }
  .bye-image{
    width: 80%;
  }
  .flower-image{
    width: 15rem;
  }
  #column-right{
    height: 90vh;
  }
  #getInTouch-center-container{
    padding: 1.5rem;
  }  
  .business-card-image{
    width: 45vw;
  }
}



/* ----------------------------------------------------------------------------------------------------- */

/* PAGE NOT FOUND */

.PageNotFound_photo_container{
  display: flex;
  justify-content: center;
  align-items: center;
}

.PageNotFound_photo_container-photo{
  padding: 30% 0;
  width: 90%;
}

@media(min-width: 800px){
  .PageNotFound_photo_container-photo{
    padding: 6rem 0;
    width: 40%;
  }
}
/* ----------------------------------------------------------------------------------------------------- */

/* FOOTER */
footer{
  font-size: .8rem;
  font-family: var(--nav-font);
  font-weight: bold;
  text-align: center;
  color: hsla(44, 100%, 45%, 1);
  padding: .8rem 0;
  margin-top: auto;
  
}

